.portfolioWrap{
    text-align: center;
}
.portfolioWrap h2{
    font-size: 1.3em;
}
.portfolioWrap a{
    font-weight: bold;
    display: inline-block;
    color: white;
    font-size: 1.2em;
    background-color: #3E2A6F;
    border: 1px solid transparent;
    border-radius: 30px;
    padding: 5px 20px;
    line-height: 2em;
}
.portfolioWrap .youtube{
    margin-left: 10px;
}
.portfolioWrap a:hover{
    font-weight: bold;
    color: #F90791;
    background-color: #3E2A6F;
    scale: calc(0.95);
}
.slick-arrow{
    position: absolute;
    top: 50%;
    z-index: 1000;
}
.slick-prev{
    left: 0;
}
.slick-next{
    right: 0;
}
.slick-slide img{
    display: inline-block;
    /* width: 100%; */
    height: 500px;
}
.slick-dots
{
    margin-bottom: -20px;
}